<template>
	<div class="intro" v-cloak>
		<!-- 解决手机端强制横屏后，一进入页面，本页在最底部的BUG -->
		<div id="top"></div>
		<div class="intro_top_word">
			<div class="intro_white_bg"></div>
			<div class="intro_top_word_body animated fadeInRightBig">
				<div class="intro_top_title">
					<span class="intro_name">{{ isResume ? '李女士' : 'F.U.N' }}</span>
					<span class="intro_birth">
						<span class="sm_word">1992</span>
						<span class="lg_word">年</span>
					</span>
					<span class="intro_content">集UI设计、前端开发技能于一体，工作以来所有技能皆是自学所得</span>
				</div>
				<div class="intro_top_content">
					<div class="intro_top_row row_space_between" v-for="(item, index) in selfEvaluation.contents"
						:key="index">
						<div class="left">{{index + 1}}</div>
						<div class="right" v-html="item"></div>
					</div>
				</div>
			</div>
		</div>
		<!-- 装饰图 -->
		<div class="animated fadeInLeftBig">
			<img :src="require(`@/assets/intro_img_1.png`)" class="intro_img_1">
			<img :src="require(`@/assets/intro_img_2.png`)" class="intro_img_2">
		</div>

		<div class="intro_others">
			<div class="intro_img_3_wrap animated fadeInUpBig">
				<img :src="require(`@/assets/intro_img_3.png`)" class="intro_img_3">
			</div>
			<div class="intro_middle row_space_between margin_side animated fadeInUpBig">
				<div class="intro_middle_block">
					<div class="intro_title_1">
						<span class="intro_title_txt">{{ hobbies.tit }}</span>
						<span class="intro_title_line"></span>
					</div>
					<div class="intro_tag_wrap" v-for="(item, index) in hobbies.content[0]" :key="index">
						<span class="left">{{ item.tip }}：</span>
						<span class="right">{{ item.detail }}</span>
					</div>
				</div>

				<div class="intro_middle_block center">
					<div class="intro_title_1">
						<span class="intro_title_txt">联系方式</span>
						<span class="intro_title_line"></span>
					</div>
					<div class="">
						<img v-lazy="require(`@/assets/intro_img_4.png`)" class="intro_img_4">
					</div>
				</div>
				<div class="intro_middle_block">
					<div class="intro_title_1">
						<span class="intro_title_txt">{{ education.tit }}</span>
						<span class="intro_title_line"></span>
					</div>
					<div class="intro_tag_wrap" v-for="(item, index) in education.content[0]" :key="index">
						<span class="left">{{item.tip}}：</span>
						<span class="right">{{item.detail}}</span>
					</div>
				</div>
			</div>

			<div class="intro_bottom">
				<div class="intro_bottom_bg"></div>
				<div class="intro_resume margin_side">
					<div class="intro_title_2">
						<div class="intro_title_inner">
							<div class="intro_title_lg_word">{{ isResume ? 'RESUME' : 'SYNTHETICAL' }}</div>
							<div class="intro_title_1">
								<span class="intro_title_txt">{{ isResume ? '简历' : '综合' }}</span>
								<span class="intro_title_line"></span>
							</div>
						</div>
						<div class="intro_title_2_line"></div>
					</div>
					<div class="intro_jobs" v-if="isResume">
						<div class="intro_title_3">
							<span class="intro_title_3_line"></span>
							<span>{{ workExperience.tit }}</span>
						</div>
						<div class="intro_job_body row_space_between">
							<div class="intro_job_block" v-for="(company, index) in workExperience.content"
								:key="index">
								<div class="intro_tag_wrap" v-for="(item, itemIndex) in company" :key="itemIndex">
									<span class="left">{{ !isResume && itemIndex === 0 ? item.tip2 : item.tip }}：</span>
									<span class="right" :class="{blue: itemIndex === 0}"
										v-html="!isResume && itemIndex === 0 ? item.detail2 : item.detail"></span>
								</div>
							</div>
						</div>
					</div>

					<div class="intro_skills">
						<div class="intro_title_1 center">
							<span class="intro_title_txt">{{ skills.tit }}</span>
							<span class="intro_title_line"></span>
						</div>
						<div class="intro_skills_wrap row_space_between">
							<div class="intro_skills_block" v-for="(skill, index) in skills.content" :key="index"
								:class="{yellow: skill[1].detail > 85}">
								<div class="intro_tag_wrap">
									<span class="right blue">{{skill[0].detail}}</span>
								</div>
								<div class="intro_tag_wrap row_space_between">
									<span class="left">{{ skill[1].tip }}：</span>
									<span class="right">{{skill[1].detail}}% <span
											class="intro_tag">{{getSkillLevel(skill[1].detail)}}</span></span>
								</div>
								<div class="bar">
									<div class="bar_inner" :style="{width:`${skill[1].detail}%`}"></div>
								</div>
							</div>
						</div>
					</div>

					<div class="intro_items">
						<div class="row_space_between">
							<div class="intro_title_3">
								<span class="intro_title_3_line"></span>
								<span>{{ projects.tit }}（{{ projects.content.length }}个）</span>
							</div>
							<div class="intro_tab_wrap" id="tab">
								<span :class="{active: activeTab === 'mixed'}" @mouseover="changeTab('mixed')"
									@click="changeTab('mixed')">复合型({{ mixedProjectsCount }}个)</span>
								<span :class="{active: activeTab === 'ui'}" @mouseover="changeTab('ui')"
									@click="changeTab('ui')">纯UI({{ uiProjectsCount }}个)</span>
							</div>
						</div>
						<div class="intro_job_body" :class="{'two': activeTab === 'ui'}"
							v-if="visibleProjects.length > 0">
							<div class="intro_job_block row_space_between" v-for="(project, index) in visibleProjects"
								:key="index">
								<div
									>
									<div class="intro_tag_wrap" v-for="(item, itemIndex) in project" :key="itemIndex"
										v-if="item.tip !== '作品演示'">
										<span class="left" v-if="item.tip !== '作品展示'">{{ item.tip }}：</span>
										<span class="tag yellow"
											:class="{'yellow': tag === 'UI', 'green': tag === 'FE', 'tag_blue': tag === 'WD'}"
											v-for="(tag, idx) in item.detail.split('|')"
											v-if="item.tip.indexOf('负责标签') > -1">{{ tag }}</span>

										<div class="project_img_wrap" v-if="item.img"
											v-for="(picSrc, picIdx) in item.img" :key="picIdx">
											<img v-lazy="`./static/smPicImgs/${picSrc}`" alt="" class="project_img"
												@click="openProjectLightbox(project, picIdx)" />
										</div>

										<span class="right" :class="{blue: itemIndex === 0}" v-html="item.detail"
											v-if="item.tip.indexOf('负责标签') === -1"></span>
									</div>
								</div>
								<video v-if="project.some(item => item.video)" id="wordVideo" class="word_video"
									autoplay="autoplay" :src="project.find(item => item.video).video" loop="loop" muted
									controls="controls"></video>
							</div>
						</div>

						<div class="intro_empty" v-else>
							<img :src="require(`@/assets/blank.png`)" alt="" />
							<div>-- 暂无项目 --</div>
						</div>

						<div class="load_more_btn" :class="{active: showAll}" v-if="hiddenProjectsCount > 0"
							@click="showAllProjects">
							<span v-if="showAll">收起</span>
							<span v-else>展开更多（+{{hiddenProjectsCount}}）</span>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div class="fixed-btn top" @click="toTop()" v-if="!isMobile">
			<img :src="require(`@/assets/arrow.png`)">
		</div>
		<div class="fixed-btn back" @click="$router.go(-1)" v-if="!isMobile">
			<img :src="require(`@/assets/arrow.png`)">
		</div>

		<!-- 全屏图片查看器 -->
		<div class="lightbox" v-if="showLightbox">
			<button class="lightbox-close" @click="closeLightbox">×</button>
			<button class="lightbox-nav lightbox-prev" @click.stop="prevImage" :disabled="lightboxIndex === 0">
				<img :src="require(`@/assets/arrow.png`)" class="nav-arrow">
			</button>
			<button class="lightbox-nav lightbox-next" @click.stop="nextImage"
				:disabled="lightboxIndex === currentProjectImages.length - 1">
				<img :src="require(`@/assets/arrow.png`)" class="nav-arrow">
			</button>

			<div class="lightbox-content" @click.self="closeLightbox">
				<img v-lazy="currentProjectImages[lightboxIndex]" class="lightbox-image" @click.stop>
			</div>
		</div>
	</div>
</template>

<script type="text/ecmascript-6">
	import db from "@/common/js/db.js";

	export default {
		name: 'intro',
		data() {
			return {
				hobbies: {},
				education: {},
				workExperience: {},
				skills: {},
				projects: {},
				selfEvaluation: {},
				showAll: false,
				isResume: true,
				activeTab: 'mixed',
				showLightbox: false,
				lightboxIndex: 0,
				currentProjectImages: []
			}
		},
		computed: {
			visibleProjects() {
				let num = this.activeTab === 'mixed' ? 3 : 2
				return this.showAll ? this.filteredProjects : this.filteredProjects.slice(0, num);
			},
			hiddenProjectsCount() {
				let num = this.activeTab === 'mixed' ? 3 : 2
				return this.filteredProjects.length - num;
			},
			filteredProjects() {
				return this.projects.content.filter(project => {
					const tagsItem = project.find(item => item.tip === '负责标签')
					let isUI = tagsItem.detail === 'UI'
					return tagsItem && (this.activeTab === 'mixed' ? !isUI : isUI)
				})
			},
			uiProjectsCount() {
				return this.projects.content.filter(project => {
					const tagsItem = project.find(item => item.tip === '负责标签');
					let isUI = tagsItem.detail === 'UI'
					return tagsItem && isUI
				}).length;
			},
			mixedProjectsCount() {
				return this.projects.content.length - this.uiProjectsCount
			},
			isMobile() {
				return window.innerWidth < 768; // 通常768px是移动和桌面的分界点
			}
		},
		created() {
			const dbData = db();
			const infos = dbData.info;
			this.isResume = dbData.type === 'resume'
			let hobbyTit = this.isResume ? '求职意向' : '个人爱好'
			let eduTit = this.isResume ? '教育背景' : 'Logo拆解'

			this.hobbies = infos.find(item => item.tit === hobbyTit) || {};
			this.education = infos.find(item => item.tit === eduTit) || {};
			this.workExperience = infos.find(item => item.tit === '工作经历') || {};
			this.skills = infos.find(item => item.tit === '职业技能') || {};
			this.projects = infos.find(item => item.tit === 'UI设计/前端主要项目') || {};
			let selfEvaluation = infos.find(item => item.tit === '自我评价') || {};
			this.selfEvaluation = selfEvaluation.content[0][0]
		},
		mounted() {
			this.mobileToTop()
			window.addEventListener('keydown', this.handleKeydown);
		},
		beforeDestroy() {
			window.removeEventListener('keydown', this.handleKeydown);
		},
		methods: {
			// 解决手机端强制横屏后，一进入页面，本页在最底部的BUG
			mobileToTop(){
				if(!this.isMobile){ return }
				this.$nextTick(()=>{
					this.$nextTick(() => {
						// 滚动条固定到定位元素位置
					    const tabElement = document.getElementById('top');
					    if (tabElement) {
					      tabElement.scrollIntoView({
					        behavior: 'auto',
					        inline: 'nearest'
					      });
					    }
					});
				})
			},
			getSkillLevel(score) {
				if (score > 85) return '精通';
				if (score >= 70) return '熟练';
				return '掌握';
			},
			showAllProjects() {
				this.showAll = !this.showAll;
				  if(!this.showAll){ return }
				  if(!this.isMobile){ return }
				  // 解决手机端横屏后，点击按钮，页面滚动位置不对的BUG
				  this.$nextTick(() => {
				    // 获取所有项目元素
				    const projectElements = document.querySelectorAll('.intro_job_block');
				    let num = this.activeTab === 'ui' ? 2 : 3
				    // 如果有4个或更多项目，滚动到第4个项目
				    if (projectElements.length >= num) {
				      projectElements[num-2].scrollIntoView({
				        behavior: 'auto',
				        block: 'start'
				      });
				    }
				    // 如果少于4个项目，滚动到最后一个项目
				    else if (projectElements.length > 0) {
				      projectElements[projectElements.length - 2].scrollIntoView({
				        behavior: 'auto',
				        block: 'start'
				      });
				    }
				  });
			},
			toTop() {
				window.scrollTo({
					top: 0,
					behavior: 'smooth'
				});
			},
			changeTab(tab) {
				this.activeTab = tab;
				this.showAll = false;
				if(!this.isMobile){ return }
				// 解决手机端横屏后，点击按钮，页面滚动位置不对的BUG
				this.$nextTick(() => {
				    const tabElement = document.getElementById('tab');
				    if (tabElement) {
				      tabElement.scrollIntoView({
				        behavior: 'auto',
				        block: 'center'
				      });
				    }
				});
			},
			openProjectLightbox(project, imgIndex = 0) {
				if (this.isMobile) {
					this.$toast.info('手机端暂不支持预览，可移至“设计”页面查看')
					return
				}
				this.currentProjectImages = project
					.filter(item => item.img)
					.flatMap(item => item.img.map(pic => `./static/picImgs/${pic}`));
				
				if (this.currentProjectImages.length > 0) {
					this.lightboxIndex = imgIndex;
					this.showLightbox = true;
					document.body.style.overflow = 'hidden';
				}
			},
			closeLightbox() {
				this.showLightbox = false;
				document.body.style.overflow = '';
			},
			prevImage() {
				if (this.lightboxIndex > 0) {
					this.lightboxIndex--;
				}
			},
			nextImage() {
				if (this.lightboxIndex < this.currentProjectImages.length - 1) {
					this.lightboxIndex++;
				}
			},
			handleKeydown(e) {
				if (!this.showLightbox) return;
				if (e.key === 'Escape') {
					this.closeLightbox();
				} else if (e.key === 'ArrowLeft') {
					this.prevImage();
				} else if (e.key === 'ArrowRight') {
					this.nextImage();
				}
			}
		}
	}
</script>

<style scoped lang="less">
