		z-index: 2;
	}

	.intro_white_bg {
		position: absolute;
		right: -27%;
		top: -95vw;
		width: 120%;
		height: 150vw;
		background: #fff;
		border-radius: 50%;
		transform: rotate(-90deg);
		z-index: 0;
	}

	.intro_top_word_body {
