		display: inline-block;
		text-align: center;
		color: @_blue;
		vertical-align: middle;

		.sm_word {
			font-size: 9px;
			display: block;
			margin-top: -3px;
			font-weight: bold;
		}

		.lg_word {
			font-size: 22px;
			display: block;
			margin-top: -5px;
		}
	}

	.intro_content {
		color: #fff;
		display: inline-block;
		background: linear-gradient(90deg, @_blue, @_dark_blue);
		border-radius: 30px;
		padding: 8px 30px;
		font-size: 14px;
	}

	.intro_top_content {
		margin-top: 30px;
	}

	.intro_top_row {
		margin-top: 25px;

		@size: 32px;

		.left {
			width: @size;
			height: @size;
			line-height: @size;
			border-radius: 50%;
			background: lighten(@_light_blue, 2%);
