			border: 8px solid darken(@_light_blue, 5%);
			text-align: center;
			font-size: 18px;
			font-weight: bold;
		}

		.right {
			width: calc(100% - @size - 50px);
			margin-top: 5px;
			line-height: 35px;
			font-size: 18px;
			color: rgba(0, 0, 0, 0.9);
		}
	}

	.intro_others {
		clear: both;
		position: relative;
		z-index: 3;
	}

	.intro_img_3 {
		width: 6%;
		vertical-align: bottom;
	}

	.intro_img_3_wrap {
		text-align: right;
		margin: 0 13%;
	}

	.margin_side {
		margin: 0 7%;
	}

	.intro_middle_block {
		background: #fff;
		border-radius: 20px;
		padding: 50px 50px 35px 50px;
		width: 1/4.5*100%;
		box-shadow: 0 0 15px 15px rgba(35, 27, 111, 0.01);

		&:hover {
			box-shadow: none;
		}
	}

	.intro_title_1 {
		margin-bottom: 20px;
	}

	.intro_title_line {
		background: @_blue;
		height: 8px;
		width: 60px;
		border-radius: 6px;
		display: inline-block;
		margin-top: 12px;
	}

	.intro_title_txt {
		font-size: 30px;
		display: block;
	}

	.center {
		text-align: center;
	}

	.intro_img_4 {
		width: 70%;
		margin-top: 5px;
	}

	.intro_tag_wrap {
		margin-top: 10px;
		line-height: 37px;

		.left {
			color: #aaa;
			display: inline-block;
			width: 70px;
			font-size: 14px;
		}

		.right {
			font-size: 16px;
		}
	}

	.intro_bottom {
		margin-top: 250px;
		position: relative;
		background: @_dark_blue;
		padding-bottom: 1px;

		.intro_title_1 {
			color: #fff;
		}

		.intro_tag_wrap {
			.left {
				color: rgba(255, 255, 255, 0.5);
			}

			.right {
				color: #fff;
			}
		}
	}

	.intro_bottom_bg {
		position: absolute;
		background: @_dark_blue;
		width: 400vw;
		height: 400vw;
		border-radius: 50%;
		top: -3vw;
		left: -150%;
		z-index: 0;
	}

	.intro_resume {
		position: relative;
		z-index: 1;
	}

	.intro_title_2 {
		text-align: center;
		position: relative;
		margin-top: -100px;
		margin-bottom: 20px;

		.intro_title_1 {
			position: absolute;
			z-index: 2;
			top: 70px;
			left: 0;
			text-align: center;
			width: 100%;
		}

		.intro_title_txt {
			font-size: 45px;
		}
	}

	.intro_title_lg_word {
		font-size: 130px;
		color: rgba(255, 255, 255, 0.04);
		background: @_dark_blue;
		display: inline-block;
		padding: 0 5%;
	}

	.intro_title_inner {
		position: relative;
		z-index: 2;
	}

	.intro_title_2_line {
		position: absolute;
		border-radius: 10px;
		background: rgba(255, 255, 255, 0.04);
		width: 100%;
		height: 12px;
		z-index: 1;
		top: 50%;
		left: 0;
	}

	.intro_title_3 {
		color: #fff;
		font-size: 25px;
		margin-bottom: 30px;

		span {
			display: inline-block;
			vertical-align: middle;
		}
	}

	.intro_title_3_line {
		background: @_blue;
		width: 8px;
		height: 30px;
		border-radius: 10px;
		margin-right: 20px;
	}

	.intro_job_block {
		width: 1/3.5*100%;
		align-items: start;
	}

	.blue {
		color: @_blue !important;
		font-weight: bold;
		font-size: 18px !important;
	}

	.intro_skills {
		background: rgba(255, 255, 255, 0.1);
		border: 5px dashed rgba(255, 255, 255, 0.2);
		border-radius: 20px;
		margin: 50px 0;
		padding: 40px 60px 30px 60px;
	}

	.bar {
		background: #fff;
		height: 10px;
		border-radius: 10px;
		margin-top: 20px;
	}

	.bar_inner {
		background: @_blue;
		height: 10px;
		border-radius: 10px;
	}
	.intro_skills_wrap{
		flex-wrap: wrap;
		align-items: end;
	}

	.intro_skills_block {
		// display: inline-block;
		width: 1/5*100%;
		// margin-right: 1/5/2*100%;
		margin-top: 50px;
		margin: 0 1% 20px;

		// &:nth-child(4n) {
		// 	margin-right: 0;
		// }

		&.yellow {
			.blue {
				color: @_yellow !important;
			}

			.bar_inner {
				background: @_yellow;
			}

			.intro_tag {
				background: lighten(@_yellow, 20%);
				color: darken(@_yellow, 14%);
			}
		}
	}

	.intro_tag {
		background: lighten(@_blue, 38%);
		color: @_blue;
		font-size: 12px;
		border-radius: 4px;
		padding: 5px 15px;
		margin-left: 10px;
		font-weight: bold;
	}

	.intro_items {
		.intro_job_block {
			width: 100%;
			border-bottom: 5px dashed rgba(255, 255, 255, 0.2);
			margin-bottom: 40px;
			padding-bottom: 30px;

			&:last-child {
				border-bottom: none;
			}
		}
	}

	.load_more_btn {
		background: lighten(@_dark_blue, 55%);
		color: @_dark_blue;
		text-align: center;
		font-size: 12px;
		border-bottom-left-radius: 10px;
		border-bottom-right-radius: 10px;
		padding: 5px 0;
		margin: -30px 0 80px 0;
		cursor: pointer;
		transition: all 0.3s;

		&:hover {
			background: lighten(@_dark_blue, 45%);
		}

		&.active {
			background: rgba(255, 255, 255, 0.2);
			color: @_yellow;
		}
	}

	.word_video {
		width: 35%;
		height: 400px;
		margin-left: 50px;
		border-radius: 10px;
		background: rgba(255, 255, 255, 0.1);
	}

	.project_img_wrap {
		width: 200px;
		max-height: 150px;
		overflow: hidden;
		border-radius: 0 8px 8px 8px;
		display: inline-block;
		vertical-align: top;
		margin-top: 15px;
		margin-right: 15px;
		transition: all 0.3s;
		cursor: zoom-in;

		&:hover,
		&:active {
			transform: scale(1.1, 1.1);
		}
	}

	.project_img {
		width: 100%;
		height: inherit;
		border-radius: 0 8px 8px 8px;
		vertical-align: top;
	}

	.short {
		width: 80%;
	}

	.tag {
		width: 22px;
		height: 22px;
		line-height: 22px;
		text-align: center;
		color: #fff;
		border-radius: 50%;
		display: inline-block;
		font-size: 12px;
		font-weight: bold;
		margin-right: 5px;

		&.yellow {
			background: #e79d26;
		}

		&.green {
			background: #71ca2c;
		}

		&.tag_blue {
			background: #9560ff;
		}
	}

	.fixed-btn {
		position: fixed;
		right: 20px;
		z-index: 10;
		background: rgba(255, 255, 255, 0.3);

		&.top {
			bottom: 70px;
			background: #fbc977;

			img {
				transform: rotate(90deg);
			}
		}

		&.back {
			background: #8cc8ef;
			bottom: 20px;
		}
	}

	.intro_tab_wrap {
		span {
			display: inline-block;
			padding: 10px 5px;
			color: #fff;
			background: #eeb76a;
			cursor: pointer;
			font-size: 14px;
			font-weight: bold;
			opacity: 0.7;
			text-align: center;
			width: 10vw;

			&:first-child {
				border-top-left-radius: 30px;
				border-bottom-left-radius: 30px;
			}

			&:last-child {
				border-top-right-radius: 30px;
				border-bottom-right-radius: 30px;
			}

			&.active {
				background: #24A4EF;
				opacity: 1;
			}
		}
	}

	.intro_empty {
		text-align: center;
		background: rgba(255, 255, 255, 0.1);
		border: 5px dashed rgba(255, 255, 255, 0.2);
		border-radius: 20px;
		margin-bottom: 40px;
		padding: 40px 60px 50px 60px;
		font-size: 16px;
		color: rgba(255, 255, 255, 0.5);

		img {
			width: 25%;
		}
	}

	.intro_job_body {
		&.two {
			.row_space_between;
			flex-wrap: wrap;

			.intro_job_block {
				width: 48%;

				&:last-child {
					border-bottom: 5px dashed rgba(255, 255, 255, 0.2);
				}
			}
		}
	}

	/* 新增轮播图样式 */
	.lightbox {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.9);
		display: flex;
		justify-content: center;
		align-items: center;
		z-index: 1000;
		cursor: zoom-out;
		backdrop-filter: blur(5px);
	}

	.lightbox-content {
		position: relative;
		width: 85%;
		height: inherit;
		display: flex;
		justify-content: center;
		align-items: start;
		overflow: auto;
	}

	.lightbox-image {
		width: 100%;
		object-fit: contain;
		cursor: default;
		margin: 40px 0;
	}

	.lightbox-close {
		position: fixed;
		top: 20px;
		right: 20px;
		background: none;
		border: none;
		color: white;
		font-size: 40px;
		cursor: pointer;
		z-index: 1001;
		padding: 10px 20px;

		&:hover {
			opacity: 0.8;
		}
	}

	.lightbox-nav {
		position: fixed;
		top: 50%;
		transform: translateY(-50%);
		background: rgba(0, 0, 0, 0.5);
		border: none;
		color: white;
		font-size: 30px;
		width: 60px;
		height: 60px;
		border-radius: 50%;
		display: flex;
		justify-content: center;
		align-items: center;
		cursor: pointer;
		z-index: 1001;

		&:hover {
			background: rgba(0, 0, 0, 0.8);
		}

		&[disabled] {
			opacity: 0.3;
			cursor: not-allowed;
		}
	}

	.lightbox-prev {
		left: 20px;

		.nav-arrow {
			transform: rotate(0deg);
		}
	}

	.lightbox-next {
		right: 20px;

		.nav-arrow {
			transform: rotate(180deg);
		}
	}

	.nav-arrow {
		width: 30px;
		height: 30px;
	}

	/* 手机端横屏适配 */
	@media screen and (orientation:portrait) {
		.intro {
			width: 100vh;
			overflow-y: hidden;
			transform-origin: 0 0;
			transform: rotateZ(90deg) translateY(-100%);
		}

		.lightbox {
			transform: rotate(0deg) translateY(0);
			transform-origin: 0 0;
			width: 100vh;
			height: 100vw;
		}

		.lightbox-content {
			width: 75%;
		}

		.lightbox-image {
			margin: 20px 0;
		}

		.intro_name {
			font-size: 22px;
		}

		.intro_birth {
			padding: 0 0;
			margin: 0 15px;

			.lg_word {
				font-size: 14px;
			}
		}

		.intro_content {
			font-size: 12px;
			padding: 5px 10px;
			width: 55%;
		}

		.intro_top_content {
			margin-top: 20px;
		}

		.intro_top_row {
			margin-top: 20px;
			@size: 20px;

			.left {
				width: @size;
				height: @size;
				line-height: @size;
				font-size: 14px;
				border-width: 5px;
			}

			.right {
				width: calc(100% - @size - 30px);
				margin-top: 0;
				font-size: 11px;
				line-height: 15px;
				opacity: 0.7;
			}
		}

		.intro_white_bg {
			width: 120%;
			height: 80vh;
			right: -30%;
			top: -40vh;
			transform: rotate(0deg);
		}

		.intro_middle_block {
			padding: 20px;
			width: 25%;
		}

		.intro_title_1 {
			margin-bottom: 5px;
		}

		.intro_title_txt {
			font-size: 20px;
			margin-bottom: -5px;
		}

		.intro_title_line {
			width: 30px;
			height: 4px;
		}

		.intro_tag_wrap {
			margin-bottom: 5px;
			line-height: 25px;

			.left {
				margin-bottom: 3px;
			}

			.right {
				font-size: 14px;
			}
		}

		.intro_skills_block {
			width: 30%;
			// margin-right: 4%;
			// margin-top: 25px;

			// &:nth-child(4n) {
			// 	margin-right: 4%;
			// }

			// &:nth-child(3n) {
			// 	margin-right: 0;
			// }
		}

		.intro_skills {
			padding: 30px;
		}

		.intro_bottom {
			margin-top: 150px;
		}

		.intro_title_3 {
			margin-bottom: 30px;
		}

		.intro_skills {
			margin: 40px 0;
		}

		.intro_title_2 {
			margin-bottom: 0;
		}

		.intro_job_body {
			flex-wrap: wrap;
			align-items: baseline;
		}

		.intro_job_block {
			align-items: start;
			width: 45%;

			&:last-child {
				width: 100%;
				margin-top: 40px;
			}
		}

		.intro_img_4 {
			width: 95%;
		}

		.intro_img_3_wrap {
			margin-top: 0;
		}

		.intro_tab_wrap {
			span {
				width: 15vh;
			}
		}

		.intro_tag {
			margin-left: 5px;
		}

		.project_img_wrap {
			width: 40%;
		}

		.intro_job_body {
			&.two {
				.row_space_between;
				flex-wrap: wrap;

				.intro_job_block {
					width: 100%;

					&:last-child {
						margin-top: -5px;
						border-bottom: none;
					}
				}
			}
		}
		.intro_img_2{
			top: 16vh;
		}
		.intro_top_word_body{
			margin-top: 20px;
		}
		
	}
	
	// 适配手机端非全屏浏览器
	@media screen and (min-height: 600px) and (max-height: 780px) and (max-width: 768px) {
		.intro_white_bg{
			top: -33vh;
		}
		.intro_img_3_wrap{
			padding-top: 20px;
		}
		.intro_top_word_body{
			margin-top: 20px;
			font-size: 10px;
			vertical-align: middle;
		}
		.intro_birth{
			width: 40px;
			margin: 0 5px;
		}
		.intro_tab_wrap span{
			width: 13vh;
		}
	}
	// 适配手机端非全屏浏览器(页面高度更短)
	@media screen and (min-height: 600px) and (max-height: 680px) and (max-width: 768px) {
		.intro_skills_block{
			width: 45%;
		}
		.intro_title_3{
			font-size: 22px;
		}
		.intro_title_3_line{
			margin-right: 5px;
		}
		.intro_tab_wrap span{
			width: 15vh;
			padding: 7px 5px;
		}
		.intro_white_bg{
			top: -27vh;
		}
		.intro_top_word_body {
			width: 49%;
			margin-right: 4%;
		}
	}
</style>


