<template>
    <div class="web">
        <!-- 顶部 -->
        <p class="web-title back">WEB网页</p>
        <div class="header-container">
            <div class="web-title-background">
              <div class="masterpiece-title">作品集</div>
                <p class="web-title front">WEB网页</p>
            </div>

            <div class="four-images-container">
                <div class="main-image-container">
                    <img v-lazy="require(`@/assets/web_img_1.png`)" class="main-image">
                </div>
                <div class="four-images-inner">
                    <img v-lazy="require(`@/assets/itemImgs/${image.srcCut}.jpg`)" class="small-image"
					v-for="(image, index) in carouselImages" :key="index" v-if="index < 4"
					@click="scroll(index, image)">
                </div>
            </div>
        </div>

        <!-- 主体内容 -->
        <div class="main-content">
            <div class="content-container">
                <!-- 左侧栏 -->
                <div class="sidebar">
                    <div class="sidebar-title">
                        <div class="title-wrapper">
                            <p class="title-char">预</p>
                            <p class="title-english">PREVIEW</p>
                        </div>
                        <p class="title-char">览</p>
                    </div>
                    <div class="sidebar-content">
                        <p class="content-text">
                            {{ curWeb.name }}
                        </p>
                        <div class="content-date">
                            <div v-if="curWeb.startTime">
								<p class="date">{{ curWeb.startTime }}&nbsp;</p>
								<p class="separator" v-if="curWeb.endTime">|</p>
								<p class="duration">{{ curWeb.endTime }}</p>
							</div>
                        </div>
						<!-- <div class="back-btn" @click="$router.push('/index')">
							返回主页
						</div> -->
                    </div>
                </div>

                <!-- 右侧内容 -->
                <div class="content-right">
                    <div class="project-image-container">
                        <div class="iframe_wrap" :class="{'app_wrap':curWeb.isApp}">
                            <div class="iframe_inner">
                                <div class="iframe_bg">
                                    <div class="cannot_img" v-if="(curWeb.isOnline || curWeb.isOnlyPic) && !curWeb.isApp">
                                        <!-- <div class="cannot_word">
                                            <h3>使用https协议的网站无法嵌入http资源，该网站无法在本站预览，请点击右下方按钮查看在线网站。</h3>
                                        </div> -->
                                        <img v-lazy="require(`@/assets/itemImgs/${curWeb.srcCut}.jpg`)">
                                    </div>
                                    <iframe id="iframe" name="mainIframe" @load="iframeLoad" :src="curWeb.src" frameborder="0" scrolling="auto" v-else></iframe>
                                </div>
                            </div>
                        </div>
                        <div class="project-image-shadow"></div>
                    </div>

                    <div class="project-description">
						<p class="description-text" v-if="curWeb.isOnline && !curWeb.isApp">
							https协议的网站无法嵌入http资源，该网站无法预览，只能查看图片。
						</p>
						<p class="description-text" v-if="curWeb.isOnlyPic">已下线，暂无离线版本，只能查看图片效果</p>
						<div class="preview-btn" @click="goWeb()" v-else>
							<b v-if="!curWeb.isOnline && !curWeb.isScan && !curWeb.isApp">已下线查看离线版本（电脑端点击进入大屏预览效果更佳）</b>
							<b v-if="curWeb.isOnline && !curWeb.isScan">查看线上网站（电脑端点击进入大屏预览效果更佳）</b>
							<b v-if="curWeb.isScan">扫码查看线上版本</b>
							<b v-if="curWeb.isApp && !curWeb.isScan">手机查看效果更佳</b>
						</div>
                    </div>
                </div>
            </div>
        </div>

        <!-- 轮播图 -->
        <div class="carousel-container">
			<div class="carousel-nav">
				<div class="nav-buttons">
					<img :src="require(`@/assets/arrow.png`)" class="nav-arrow" @click="slideRight" :class="{ 'disabled': isAtStart }">
					<img :src="require(`@/assets/arrow.png`)" class="nav-arrow right" @click="slideLeft" :class="{ 'disabled': isAtEnd }">
				</div>
			</div>
			<div class="carousel-images">
				<div class="carousel-track"
					:style="{ transform: `translateX(${offsetX + dragOffset}px)` }"
					@mousedown="startDrag"
					@touchstart="startDrag"
					@mousemove="onDrag"
					@touchmove="onDrag"
					@mouseup="endDrag"
					@mouseleave="endDrag"
					@touchend="endDrag">
					<div class="carousel-image-block"
						v-for="(image, index) in carouselImages"
						:key="index"
						:class="{ 'active': activeCarouselIndex === index }"
						@click="setActiveCarouselIndex(index, image)">
						<img v-lazy="require(`@/assets/itemImgs/${image.srcCut}.jpg`)" class="carousel-image">
					</div>
				</div>
			</div>
		</div>
		<div class="fixed-btn top" @click="toTop()" v-if="!isMobile">
			<img :src="require(`@/assets/arrow.png`)">
		</div>
		<div class="fixed-btn back" @click="$router.push('/index')">
			<img :src="require(`@/assets/arrow.png`)">
		</div>
    </div>
</template>

<script>
import db from "@/common/js/db";

export default {
    name: 'web',
    data() {
        return {
            curWeb: {},
            webs: db().webs,
            // 轮播图相关数据
            carouselImages: [],
            activeCarouselIndex: 0,
            currentPosition: 0,
            itemWidth: 460, // 图片宽度 + 边距
            visibleItems: 3, // 可见图片数量
            isDragging: false,
            startX: 0,
            dragOffset: 0,
            lastDragOffset: 0,
            velocity: 0,
            lastTime: 0,
            animationId: null,
			curWebIndex:0,
			curWeb:{},
			iframePageNum: 0
        }
    },
    computed: {
        offsetX() {
            return -this.currentPosition * this.itemWidth;
        },
        isAtStart() {
            return this.currentPosition <= 0;
        },
        isAtEnd() {
            return this.currentPosition >= this.carouselImages.length - this.visibleItems;
        },
		// 是否是手机端
		isMobile() {
			return window.innerWidth < 768; // 通常768px是移动和桌面的分界点
		}
    },
    created() {
		this.visibleItems = this.isMobile ? 3 : 3 // 可见图片数量:pc3，手机2
        this.itemWidth = this.isMobile ? 170 : 460, // 图片宽度 + 边距
        this.curWeb = this.webs[0] || {}; // 默认显示第一个项目
		this.carouselImages = this.webs
		this.curWebIndex = this.$route.params.itemIndex ? this.$route.params.itemIndex : 0
		this.curWeb = this.webs[this.curWebIndex]
    },
	mounted(){
		// 默认页面水平滚动到右边（适配手机端）
		window.scrollTo({
		  left: document.body.scrollWidth,
		  behavior: 'auto' // 可选，平滑滚动
		});
	},
	watch:{
	  curWebIndex(n,o){
	    this.curWeb = this.webs[this.curWebIndex]
	  }
	},
    methods: {
		toTop(){
			// 默认页面水平滚动到右边（适配手机端）
			window.scrollTo({
			  top: 0,
			  behavior: 'smooth' // 可选，平滑滚动
			});
		},
		iframeLoad(){
			this.iframePageNum ++
		},
		// 自定义返回方法
        customGoBack() {
          if (this.iframePageNum > 0) {
            // 如果 iframe 有历史记录，先返回 iframe 的历史
            const iframe = document.getElementById('iframe');
            for(let i = 0; i < this.iframePageNum - 1; i++){
              iframe.contentWindow.history.back();
            }

            this.iframePageNum = 0
          } else {
            // 否则返回 Vue 路由的历史
            this.$router.go(-1);
          }
        },
        goWeb(){
          window.open(this.curWeb.src)
        },
        // 轮播图相关方法
        setActiveCarouselIndex(index, image) {
            this.curWebIndex = index;
          	this.activeCarouselIndex = index;
          	if (index > this.currentPosition + this.visibleItems - 1) {
          		this.currentPosition = index - this.visibleItems + 1;
          	} else if (index < this.currentPosition) {
          		this.currentPosition = index;
          	}
        },
        slideLeft() {
            if (!this.isAtEnd) {
                this.currentPosition += this.visibleItems;
                if (this.currentPosition > this.carouselImages.length - this.visibleItems) {
                    this.currentPosition = this.carouselImages.length - this.visibleItems;
                }
            }
        },
        slideRight() {
            if (!this.isAtStart) {
                this.currentPosition -= this.visibleItems;
                if (this.currentPosition < 0) {
                    this.currentPosition = 0;
                }
            }
        },
        startDrag(e) {
            this.isDragging = true;
			let key = this.isMobile ? 'clientY' : 'clientX'
			this.startX = e[key] || e.touches[0][key];
            this.lastDragOffset = 0;
            this.velocity = 0;
            this.lastTime = Date.now();
            cancelAnimationFrame(this.animationId);
        },
        onDrag(e) {
            if (!this.isDragging) return;
			
			let key = this.isMobile ? 'clientY' : 'clientX'
            const x = e[key] || e.touches[0][key];
            const now = Date.now();
            const deltaTime = now - this.lastTime;

            this.dragOffset = x - this.startX;

            if (deltaTime > 0) {
                this.velocity = (this.dragOffset - this.lastDragOffset) / deltaTime;
                this.lastDragOffset = this.dragOffset;
                this.lastTime = now;
            }

            e.preventDefault();
        },
        endDrag() {
            if (!this.isDragging) return;
            this.isDragging = false;

            // 计算惯性滑动距离
            const momentumDistance = this.velocity * 50;
            const totalOffset = this.dragOffset + momentumDistance;

            // 计算应该移动的图片数量
            const itemDelta = Math.round(totalOffset / this.itemWidth);

            // 计算新位置
            let newPosition = this.currentPosition - itemDelta;
            newPosition = Math.max(0, Math.min(newPosition, this.carouselImages.length - this.visibleItems));

            this.currentPosition = newPosition;
            this.dragOffset = 0;
            this.velocity = 0;
            this.lastDragOffset = 0;
        },
		scroll(index, image){
			this.setActiveCarouselIndex(index, image)
			window.scrollTo({
			  top: 100,
			  behavior: 'smooth' // 平滑滚动
			});
		}
    }
}
</script>

