			min-height: 100px;
		}
		.description-text{
			height: 30px;
			line-height: 25px;
		}
		.preview-btn{
			font-size: 14px;
			padding: 8px 25px;
		}
		.carousel-images{
			padding: 15px 0;
		}
		.carousel-image-block{
			width: 150px;
			height: 75px;
			margin-left: 20px;
			border-radius: 10px;

			img{
				border-radius: 10px;
			}
		}
		.nav-arrow{
			width: 30px;
			height: 40px;
		}
		.preview-btn{
			&.active{
				margin-top: 10px;
			}
		}
		.separator, .duration{
			margin-top: 0;
		}
    }
	// 适配手机端非全屏浏览器(页面高度更短)
	@media screen and (min-height: 600px) and (max-height: 680px) and (max-width: 768px) {
		.project-description{
			font-size: 13px;
		}
		.preview-btn{
			font-size: 13px;
		}
		.iframe_wrap{
			height: 175px;
		}
		.project-description{
			min-height: 60px;
		}
		.sidebar{
			padding-bottom: 35vw;
		}
	}
</style>



